import React from 'react';

const Wirkung = () => {
    return (
        <main className="container content">


            <div className="jumbotron bg-info">
                <h1 className="display-3">Wirkung</h1>
                <p className="lead">Die Thai Massage ist eine natürliche, jahrtausendealte, sichere
                    und nebenwirkungsfreie Heilmethode. Die bekannteste Lehrwerkstätte dieser Methode ist die Wat
                    Po-Universität in Bangkok in Thailand. Bei dieser Massage werden über 60 verschiedene Körperpunkte- und
                    Linien einzeln und gezielt bearbeitet.</p>
            </div>


            <div className="row">
                <div className="col-md-6">
                    <h2 className="display-4">
                        Nach traditionell asiatischer Lehre
                    </h2>
                    <ul className="list-group bg-light w-100">
                        <li className="list-group-item list-group-item-light"> Kopfschmerzen</li>
                        <li className="list-group-item list-group-item-light"> Übelkeit</li>
                        <li className="list-group-item list-group-item-light"> Verstopfung</li>
                        <li className="list-group-item list-group-item-light"> Durchfall</li>
                        <li className="list-group-item list-group-item-light"> Ohrensausen (Tinnitus)</li>
                        <li className="list-group-item list-group-item-light"> Schlafstörungen</li>
                        <li className="list-group-item list-group-item-light"> Schock</li>
                        <li className="list-group-item list-group-item-light"> Husten</li>
                        <li className="list-group-item list-group-item-light"> Knieschmerzen</li>
                        <li className="list-group-item list-group-item-light"> Rückenschmerzen</li>
                        <li className="list-group-item list-group-item-light"> Schwindel</li>
                    </ul>
                </div>
                <div className="col-md-6">
                    <h2 className="display-4">
                        Aus wissenschaftlicher Sicht </h2>
                    <ul className="list-group">
                        <li className="list-group-item list-group-item-light"> Druckpunktmassagen regen die Blutzirkulation an.</li>
                        <li className="list-group-item list-group-item-light"> In der Dehnung kann die Muskulatur entspannen.</li>
                        <li className="list-group-item list-group-item-light"> Yogapositionen beeinflussen das skelettomuskuläre System und
                            wirken sich auf die
                            Körperhaltung aus.
                        </li>
                        <li className="list-group-item list-group-item-light">Drehungen, Beugungen und Streckungen der Wirbelsäule können
                            durch Zug intensiviert werden
                            und haben einen Effekt auf die körperliche Beweglichkeit.
                        </li>
                        <li className="list-group-item list-group-item-light"> Positionen, in denen die Beine angehoben werden, fördern die
                            Durchblutung und den <a target="_new"
                                                    href="/wiki/Lymphe" title="Lymphe">Lymphfluss</a>.
                        </li>
                        <li className="list-group-item list-group-item-light"> Die Rotation der Gelenke trägt zur Produktion von <a
                            target="_new"
                            href="/wiki/Synovialfl%C3%BCssigkeit"
                            title="Synovialflüssigkeit">Synovialflüssigkeit</a> und damit zu körperlicher
                            Geschmeidigkeit bei.
                        </li>
                        <li className="list-group-item list-group-item-light"> In der Tiefenentspannung wird der <a
                            target="_new"
                            href="/wiki/Parasympathikus"
                            title="Parasympathikus">Parasympathikus</a> aktiviert, sodass die inneren Organe vermehrt
                            durchblutet und der Stoffwechsel angeregt
                            werden.
                        </li>
                        <li className="list-group-item list-group-item-light"> Achtsame Berührung kann körperliches Wohlbefinden auslösen, und
                            Hebetechniken können zu
                            einem Gefühl der Geborgenheit führen.
                        </li>
                    </ul>
                </div>
            </div>


        </main>
    );
};

export default Wirkung;



