import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import './index.css';
import {BrowserRouter, Route} from 'react-router-dom';
import Wirkung from "./container/Wirkung";
import About from "./container/About";
import Karte from "./container/Karte";
import Footer from "./container/Footer";
import registerServiceWorker from "./registerServiceWorker";
import Slider from "./components/Slider";
import Clock from "./components/Clock";
import $ from 'jquery';

const Home = (props) => {
    console.log(props);
    return <div>
        <h1>Home</h1>
        <Clock/>
        <Slider/>
    </div>

}

const Contact = (props) => {
    console.log(props);
    return <h1>Contact</h1>
}

const Form = (props) => {
    console.log(props);
    return <div>
        <form method="post" action="sendmail.php">

            <div class="form-group">
                <input type="email" name="email" placeholder="Email Adresse*" required/>
            </div>

            <div class="form-group">
                <input type="text" name="subject" placeholder="Betreff*"/>
            </div>

            <div class="form-group">
                <textarea name="message" placeholder="Ihre Nachricht*"></textarea>
            </div>

            <div class="form-group">
                <button class="theme-btn btn-style-twelve" type="submit" name="submit-form">
                    NACHRICHT SENDEN
                </button>
            </div>

        </form></div>
}



ReactDOM.render(
    <BrowserRouter>
        <div>
            <Route component={App}/>
            <Route path='/home' component={Home}/>
            <Route path='/contact' component={Contact}/>
            <Route path='/wirkung' component={Wirkung}/>
            <Route path='/about' component={About}/>
            <Route path='/karte' component={Karte}/>
            <Route path='/form' component={Form}/>
            <Route component={Footer}/>

        </div>
    </BrowserRouter>,
    document.getElementById('root'));
registerServiceWorker();



