import React from 'react';

const About = () => {
    return (
        <main className="container content">
            <section>


                <div className="jumbotron bg-info">
                    <h1 className="display-3">Über uns</h1>
                    <p className="lead">Das traditionelle <a href="https://de.wikipedia.org/wiki/Thai-Massage" target="_new"
                                                             rel="follow"
                                                             title="Wikipedia Eintrah Thai-Massage"> Thai-Massage nach
                        Wat-Po-Art </a> führt
                        durch Dehnen und Drücken zu tiefe Atmung, fördert die
                        Durchblutung, aktiviert die Energiepunkte, beruhigt die Nerven und tut immer gut!
                    </p>

                </div>



            </section>
            <section className="card-deck">
                <div className="card bg-light">
                    <img className="card-img-top"
                         src={process.env.PUBLIC_URL + '/assets/images/nen.jpg'}
                         alt="Card cap 1"/>
                    <div className="card-body">
                        <h4 className="card-title">Nen</h4>
                        <p className="card-text">This is a longer card with supporting text below as a natural lead-in
                            to additional content. This content is a little bit longer.</p>
                    </div>
                </div>
                <div className="card bg-light">
                    <img className="card-img-top"
                         src={process.env.PUBLIC_URL + '/assets/images/joe.jpg'}
                         alt="Card cap 2"/>
                    <div className="card-body">
                        <h4 className="card-title">Joe</h4>
                        <p className="card-text">This card has supporting text below as a natural lead-in to additional
                            content.</p>
                    </div>
                </div>
                <div className="card   bg-light">
                    <img className="card-img-top"
                         src={process.env.PUBLIC_URL + '/assets/images/plaeu.jpg'}
                         alt="Card cap 3"/>
                    <div className="card-body">
                        <h4 className="card-title">Pläu</h4>
                        <p className="card-text">This is a wider card with supporting text below as a natural lead-in to
                            additional content. This card has even longer content than the first to show that equal
                            height action.</p>
                    </div>
                </div>
            </section>


        </main>
    );
};

export default About;



