import React from 'react';

const Footer = () => {
    return (

        <footer className="bg-secondary">
            <div className="container">
                <div className="row">
                    <h1 className="sr-only">Footer</h1>
                    <div className="col-sm-12 col-md-3">
                        <h2 className="sr-only">scial media</h2>
                        <a href="#" title="Suranaya Thaimassage Facebook Seite">
                            <img className="icon" src="assets/images/facebook.svg"
                                 alt="Facebook Seite Suranaya Thaimassage"/>
                        </a>
                        <a href="#" title="Suranaya Thaimassage Google Seite">
                            <img className="icon" src="assets/images/google.svg"
                                 alt="Google Seite Suranaya Thaimassage"/>
                        </a>
                        <h2 className="sr-only">copyright</h2>
                        <p className="text-dark">
                            <small>© 2018 Suranaya Thaimassage</small>
                        </p>
                    </div>

                    <div className="col-sm-6 col-md-6">
                        <h2 className="text-md-center display-2"><strong>Telefon <a itemProp="telephone"
                                                                       className="text-dark"
                                                               href="tel:+41797229620"
                                                                       content="+41797229620">079 722 96 20</a></strong></h2>
                        <h2 className="text-md-center display-2"><span className="sr-only">E-Mail</span> <a
                            href="mailto:info@thaimassage-suranaya.ch" itemProp="email">
                            info@thaimassage-suranaya.ch</a></h2>
                    </div>

                    <div className="col-sm-6 col-md-3">
                        <h2 className="text-md-right display-2"><strong>Adresse</strong></h2>
                        <address className="text-md-right"
                                 itemProp="address" itemScope itemType="http://schema.org/PostalAddress">
                            <span itemProp="streetAddress">Gerberngasse 16</span><br/>
                            <span itemProp="addressLocality"> 3011 Bern</span><br/>
                            <span className="sr-only" itemProp="addressRegion"> BE</span>
                            <span className="sr-only"
                                  itemProp="url"> <a href='http://www.thaimassage-suranaya.ch'>http://www.thaimassage-suranaya.ch</a></span>
                        </address>
                    </div>



                </div>
            </div>
        </footer>

    );
};
export default Footer;


