import React from 'react';
import SliderSlide from "./SliderSlide";


class Slider extends React.Component {
    constructor() {
        super();
        this.state = {
            slides: [{
                id: 1,
                addActiveClass: "active",
                path: "/assets/images/gruppe_1-16_9.jpg",
                alt: "Team 1",
            }, {
                id: 2,
                addActiveClass: "",
                path: "/assets/images/gruppe_2-16_9.jpg",
                alt: "Team 2"
            }, {
                id: 3,
                addActiveClass: "",
                path: "/assets/images/bg_3.jpg",
                alt: "Thai wallpaper 3",
                title: "Das ist Slider 3 Titel",
                caption: "DAs ist der 3. CAPTION Text."
            }, {
                id: 4,
                addActiveClass: "",
                path: "/assets/images/bg_2.jpg",
                alt: "Thai wallpaper 3",
                title: "Das ist Slider 4 Titel",
                caption: "DAs ist der 4. CAPTION Text."

            }, {
                id: 5,
                addActiveClass: "",
                path: "/assets/images/gruppe_2-16_9.jpg",
                alt: "slide 2",
                title: "Das ist Slider 5 Titel",
                caption: "Das ist der 5. CAPTION Text."
            }],
        }
    }

    render() {
        const listOfSlides = this.state.slides.map((slide) =>
            <SliderSlide
                key={this.state.slides.indexOf(slide)}
                addActiveClass={slide.addActiveClass}
                path={slide.path}
                alt={slide.alt}
                title={slide.title}
                caption={slide.caption}
            />);

        return (
            <div id="carouselExampleControls" className="carousel slide" data-ride="carousel">
                <div className="carousel-inner">
                    {listOfSlides}
                </div>
                {/*    <div className="carousel-inner">
                    <div className="carousel-item active">
                        <img className="d-block w-100"
                             src="assets/images/gruppe_1-16_9.jpg"
                             alt="1"/>
                    </div>
                    <div className="carousel-item">
                        <img className="d-block w-100"
                             src="assets/images/gruppe_2-16_9.jpg"
                             alt="2"/>
                    </div>
                    <div className="carousel-item">
                        <img className="d-block w-100"
                             src="
                             alt=""/>
                        <div className="carousel-caption d-none d-md-block">
                            <h3>Parkmöglichkeiten direkt beim Eingang</h3>
                            <p>Nahe Bushaltestelle FOOOOOOO, direkt im Zentrum, gute Parkmöglichkeiten. </p>
                        </div>
                    </div>
                    <div className="carousel-item">
                        <img className="d-block w-100"
                             src=""
                             alt=""/>
                        <div className="carousel-caption d-none d-md-block">
                            <h3>Seriöse Entspannungsmassagen</h3>
                            <p>100% Thai Massage in Bern. Entspannung garantiert. Dusche vorhanden.
                            </p>
                        </div>
                    </div>
                </div>*/}
                <a className="carousel-control-prev" href="#carouselExampleControls" role="button"
                   data-slide="prev">
                    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span className="sr-only">Previous</span>
                </a>
                <a className="carousel-control-next" href="#carouselExampleControls" role="button"
                   data-slide="next">
                    <span className="carousel-control-next-icon" aria-hidden="true"></span>
                    <span className="sr-only">Next</span>
                </a>
            </div>
        );
    }
}


export default Slider;


