import React from 'react';


class SliderSlide extends React.Component {

    /**
     * Return a rendered slide with or without caption.
     * @returns {XML} render a slide.
     */
    getSlide() {
        const slide =
            <img className="d-block w-100"
                 src={process.env.PUBLIC_URL +  this.props.path}
                 alt={this.props.alt}/>

        const caption =
            <div className="carousel-caption d-md-block">
                <h3>{this.props.title}</h3>
                <p>{this.props.caption}</p>
            </div>


        if (this.props.title) {
            return (
                <div key={this.props.id} className={`carousel-item ${ this.props.addActiveClass }`}>
                    {slide}
                    {caption}
                </div>
            );
        }
        return (
            <div key={this.props.id} className={`carousel-item ${ this.props.addActiveClass }`}>
                {slide}
            </div>
        );
    }

    render() {
        return (this.getSlide());
    }
}

export default SliderSlide;


